.exchange_header {
	margin-top: 10px;
	font-size: 40px;
	text-align: center;
	text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.exchange_box {
	width: 100%;
	height: 250px;
	background-color: #c8c7c5;
	border-radius: 60px;
	padding: 65px;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	margin-top: 30px;
	margin-bottom: 30px;
}

.exchange_title {
	font-size: 36px;
	margin-bottom: 20px;
}

.exchange_description {
	font-size: 20px;
	font-weight: bold;
}

.exchange_img {
	width: 500px;
	border-radius: 30px;
}

.exchange_success {
	width: 100%;
	height: 250px;
	border-radius: 60px;
	padding: 50px;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	gap: 20px;
	margin-bottom: 20px;
}

.exchange_success_list {
	width: 500px;
}

.exchange_success_item {
	list-style: auto;
	font-weight: bold;
	margin-top: 10px;
}

.exchange_failed {
	width: 100%;
	height: 350px;
	background-color: #c8c7c5;
	border-radius: 60px;
	padding: 50px 50px 50px 80px;
	margin-bottom: 50px;
}

.exchange_failed_title {
	text-align: center;
	font-size: 36px;
	margin-bottom: 10px;
}

.exchange_failed_list {
	padding-left: 50px;
	padding-right: 50px;
	padding-top: 5px;
}

.exchange_failed_item {
	list-style: square;
	font-weight: bold;
	margin-top: 20px;
}

.exchange_conditions {
	width: 100%;
	padding-left: 65px;
	padding-right: 65px;
	margin-bottom: 50px;
}

.exchange_conditions_title {
	font-size: 36px;
	margin-bottom: 20px;
	text-align: center;
}

.exchange_conditions_list {
    display: flex;
    flex-wrap: wrap;
	justify-content: space-around;
    gap: 10px;
}

.exchange_conditions_item {
	max-width: 550px;
    list-style: circle;
    font-weight: bold;
    margin-top: 20px;
}